import React, { useState, useEffect, useRef, useCallback, useMemo } from 'react';
import { NavLink, Outlet, useNavigate } from 'react-router-dom';
import {
  LayoutDashboard,
  Package,
  Users,
  ShoppingBag,
  BarChart,
  Settings,
  LogOut,
  Menu,
  FileText, //    
} from 'lucide-react';
import { supabase } from '../../lib/supabaseClient';
import { motion, AnimatePresence } from 'framer-motion';
import Modal from 'react-modal';
import { toast } from 'react-toastify';

Modal.setAppElement('#root');

interface NavItem {
  to: string;
  icon: JSX.Element;
  text: string;
}

export interface Session {
  id: string;
  user_id?: string;
  guest_id?: string;
  last_activity: string;
  is_online: boolean;
}

export const AdminLayout: React.FC = () => {
  const navigate = useNavigate();
  const [isSidebarOpen, setIsSidebarOpen] = useState(false);
  const [isLogoutModalOpen, setIsLogoutModalOpen] = useState(false);
  const [isLoggingOut, setIsLoggingOut] = useState(false);
  const [sessions, setSessions] = useState<Session[]>([]);
  const [isLoadingSessions, setIsLoadingSessions] = useState(true);
  const [sessionsError, setSessionsError] = useState('');
  const sidebarRef = useRef<HTMLDivElement>(null);

  //       
  const navItems = useMemo<NavItem[]>(() => [
    { to: '/admin', icon: <LayoutDashboard />, text: '' },
    { to: '/admin/products', icon: <Package />, text: '' },
    { to: '/admin/orders', icon: <ShoppingBag />, text: '' },
    { to: '/admin/sessions', icon: <Users />, text: '' },
    { to: '/admin/analytics', icon: <BarChart />, text: '' },
    { to: '/admin/settings', icon: <Settings />, text: '' },
    { to: '/admin/articles', icon: <FileText />, text: '' },
  ], []);

  const refreshSessions = useCallback(async () => {
    try {
      setIsLoadingSessions(true);
      const { data, error } = await supabase
        .from('sessions')
        .select('*')
        .order('last_activity', { ascending: false });
      if (error) throw error;
      setSessions(data || []);
      setSessionsError('');
    } catch (error) {
      setSessionsError('    ');
      console.error('  :', error);
      toast.error('   ');
    } finally {
      setIsLoadingSessions(false);
    }
  }, []);

  //  guest_id    UUID
  const generateGuestId = () => {
    return crypto.randomUUID();
  };

  //         .
  //    session_id,    .
  const updateSessionToGuest = async () => {
    try {
      const storedSessionId = localStorage.getItem('session_id');
      const now = new Date().toISOString();
      if (storedSessionId) {
        let guestId = localStorage.getItem('guest_id');
        if (!guestId) {
          guestId = generateGuestId();
          localStorage.setItem('guest_id', guestId);
        }
        const updatedSession = {
          user_id: null,
          guest_id: guestId,
          last_activity: now,
          is_online: true,
        };
        const { data, error } = await supabase
          .from('sessions')
          .update(updatedSession)
          .eq('id', storedSessionId)
          .select();
        if (error) {
          console.error('    :', error);
          toast.error(`  : ${error.message}`);
          throw error;
        }
        console.log('    :', data);
        return data;
      } else {
        //  session_id ,    
        const guestId = generateGuestId();
        const newSession = {
          user_id: null,
          guest_id: guestId,
          last_activity: new Date().toISOString(),
          is_online: true,
        };
        const { data, error } = await supabase
          .from('sessions')
          .insert(newSession)
          .select();
        if (error) {
          console.error('   :', error);
          toast.error(`  : ${error.message}`);
          throw error;
        }
        console.log('   :', data);
        if (data && data.length > 0) {
          localStorage.setItem('session_id', data[0].id);
          localStorage.setItem('guest_id', guestId);
        }
        return data;
      }
    } catch (err) {
      console.error('  updateSessionToGuest:', err);
      toast.error('     ');
      return null;
    }
  };

  //          .
  //   ,     (  )  upsert .
  const createSession = async () => {
    try {
      const { data: userData, error: userError } = await supabase.auth.getUser();
      if (userError) {
        console.error('  :', userError);
        throw userError;
      }
      const user = userData?.user;
      const now = new Date().toISOString();

      if (user) {
        //  
        const guestId = localStorage.getItem('guest_id');
        if (guestId) {
          const { data: migratedId, error: migrateError } = await supabase.rpc('migrate_guest_data', {
            p_guest_id: guestId,
            p_user_id: user.id,
          });
          if (migrateError) {
            //     ,  guest_id    
            if (migrateError.message.includes("duplicate key value violates unique constraint")) {
              localStorage.removeItem('guest_id');
            } else {
              console.error(' :', migrateError);
              toast.error(` : ${migrateError.message}`);
            }
          } else if (migratedId) {
            toast.success('   ');
            localStorage.removeItem('guest_id');
            localStorage.setItem('session_id', migratedId as string);
            //     ,     
            if (!localStorage.getItem('loggedInToastShown')) {
              toast.success('  ');
              localStorage.setItem('loggedInToastShown', 'true');
            }
            return migratedId;
          }
        }
        //  upsert   
        const newSession = {
          user_id: user.id,
          guest_id: null,
          last_activity: now,
          is_online: true,
        };
        const { data, error } = await supabase
          .from('sessions')
          .upsert(newSession, { onConflict: 'user_id' })
          .select();
        if (error) {
          console.error(' / :', error);
          toast.error(` : ${error.message}`);
          throw error;
        }
        console.log('  /:', data);
        if (data && data.length > 0) {
          localStorage.setItem('session_id', data[0].id);
        }
        if (!localStorage.getItem('loggedInToastShown')) {
          toast.success('  ');
          localStorage.setItem('loggedInToastShown', 'true');
        }
        return data;
      } else {
        //   :     
        return await updateSessionToGuest();
      }
    } catch (err) {
      console.error('  createSession:', err);
      toast.error('   ');
      return null;
    }
  };

  useEffect(() => {
    let isMounted = true;
    const fetchData = async () => {
      try {
        if (isMounted) {
          await createSession();
          await refreshSessions();
        }
      } catch (err) {
        console.error('  fetchData:', err);
        if (isMounted) {
          setSessionsError('     ');
        }
      }
    };

    fetchData();
    const interval = setInterval(fetchData, 10000);
    return () => {
      isMounted = false;
      clearInterval(interval);
    };
  }, [refreshSessions]);

  const sessionStats = useMemo(() => ({
    total: sessions.length,
    active: sessions.reduce((acc, s) => s.is_online ? acc + 1 : acc, 0),
    guests: sessions.reduce((acc, s) => s.guest_id ? acc + 1 : acc, 0),
    users: sessions.reduce((acc, s) => s.user_id ? acc + 1 : acc, 0)
  }), [sessions]);

  const handleClickOutside = useCallback((event: MouseEvent) => {
    if (sidebarRef.current && !sidebarRef.current.contains(event.target as Node)) {
      setIsSidebarOpen(false);
    }
  }, []);

  useEffect(() => {
    if (isSidebarOpen) {
      document.addEventListener('mousedown', handleClickOutside);
    }
    return () => document.removeEventListener('mousedown', handleClickOutside);
  }, [isSidebarOpen, handleClickOutside]);

  //          
  const handleLogout = async () => {
    try {
      setIsLoggingOut(true);
      await updateSessionToGuest();
      const { error } = await supabase.auth.signOut();
      if (error) throw error;
      localStorage.removeItem('sb-auth-token');
      sessionStorage.removeItem('sb-session');
      //  ,      
      localStorage.removeItem('loggedInToastShown');
      navigate('/', { replace: true });
      toast.success('  ');
    } catch (_error) {
      toast.error('  . ,  .');
    } finally {
      setIsLoggingOut(false);
      setIsLogoutModalOpen(false);
    }
  };

  const sidebarVariants = {
    open: { x: 0 },
    closed: { x: '-100%' },
  };

  const renderNavLink = useCallback(({ to, icon, text }: NavItem) => (
    <NavLink
      key={to}
      to={to}
      onClick={() => setIsSidebarOpen(false)}
      className={({ isActive }) =>
        `flex items-center px-4 py-2 rounded-lg transition-colors 
         ${isActive ? 'bg-blue-800 text-white' : 'text-gray-300 hover:bg-blue-800 hover:text-white'}`
      }
    >
      {React.cloneElement(icon, { className: 'w-5 h-5 mr-3' })}
      {text}
    </NavLink>
  ), []);

  return (
    <div className="min-h-screen bg-gray-100 relative overflow-hidden">
      <button
        onClick={() => setIsSidebarOpen(!isSidebarOpen)}
        className="absolute top-4 right-4 p-2 bg-blue-900 text-white rounded-lg z-50 lg:hidden"
        aria-label=""
      >
        <Menu className="w-6 h-6" />
      </button>

      <div className="flex relative">
        <motion.div
          ref={sidebarRef}
          className="hidden lg:block w-64 bg-blue-900 min-h-screen p-4 z-40"
          initial="open"
          animate="open"
        >
          <div className="text-white font-bold text-xl mb-8 mt-16">SHINAGO Admin</div>
          <nav className="space-y-2">
            {navItems.map(renderNavLink)}
            <div className="px-4 py-2 space-y-2 text-gray-300">
              {isLoadingSessions ? (
                <div className="animate-pulse flex space-x-4">
                  <div className="flex-1 space-y-2 py-1">
                    <div className="h-4 bg-blue-800 rounded"></div>
                    <div className="h-4 bg-blue-800 rounded"></div>
                  </div>
                </div>
              ) : sessionsError ? (
                <div className="text-red-300 text-sm">{sessionsError}</div>
              ) : (
                <div className="grid grid-cols-2 gap-2 text-sm">
                  <div className={`p-2 rounded ${sessionStats.active > 0 ? 'bg-green-500' : 'bg-gray-500'} text-white`}>
                    : {sessionStats.active}
                  </div>
                  <div className="p-2 rounded bg-blue-500 text-white">
                    : {sessionStats.total}
                  </div>
                  <div className="p-2 rounded bg-purple-500 text-white">
                    : {sessionStats.guests}
                  </div>
                  <div className="p-2 rounded bg-orange-500 text-white">
                    : {sessionStats.users}
                  </div>
                </div>
              )}
            </div>
            <button
              onClick={() => setIsLogoutModalOpen(true)}
              className="flex items-center text-gray-300 hover:text-white hover:bg-blue-800 px-4 py-2 rounded-lg w-full transition-colors"
            >
              <LogOut className="w-5 h-5 mr-3" />
              
            </button>
          </nav>
        </motion.div>

        <AnimatePresence>
          {isSidebarOpen && (
            <motion.div
              ref={sidebarRef}
              variants={sidebarVariants}
              initial="closed"
              animate="open"
              exit="closed"
              className="fixed lg:hidden w-64 bg-blue-900 min-h-screen p-4 z-40"
            >
              <div className="text-white font-bold text-xl mb-8 mt-16">SHINAGO Admin</div>
              <nav className="space-y-2">
                {navItems.map(renderNavLink)}
                <button
                  onClick={() => setIsLogoutModalOpen(true)}
                  className="flex items-center text-gray-300 hover:text-white hover:bg-blue-800 px-4 py-2 rounded-lg w-full transition-colors"
                >
                  <LogOut className="w-5 h-5 mr-3" />
                  
                </button>
              </nav>
            </motion.div>
          )}
        </AnimatePresence>

        <div className="flex-1 p-8 mt-16">
          {sessionsError ? (
            <div className="bg-red-50 text-red-600 p-4 rounded-lg">
              {sessionsError}
            </div>
          ) : (
            <Outlet context={{ sessions, refreshSessions, isLoading: isLoadingSessions }} />
          )}
        </div>
      </div>

      <Modal
        isOpen={isLogoutModalOpen}
        onRequestClose={() => setIsLogoutModalOpen(false)}
        shouldCloseOnOverlayClick={true}
        className="modal"
        overlayClassName="modal-overlay"
        ariaHideApp={true}
        contentLabel=" "
      >
        <h2 className="text-xl font-bold mb-4"> ,   ?</h2>
        <div className="flex justify-end space-x-4">
          <button
            onClick={() => setIsLogoutModalOpen(false)}
            className="px-4 py-2 bg-gray-300 rounded-lg hover:bg-gray-400 transition-colors"
          >
            
          </button>
          <button
            onClick={handleLogout}
            disabled={isLoggingOut}
            className="px-4 py-2 bg-red-500 text-white rounded-lg flex items-center gap-2 hover:bg-red-600 transition-colors"
          >
            {isLoggingOut && (
              <div className="w-4 h-4 border-2 border-white rounded-full animate-spin" />
            )}
            {isLoggingOut ? '...' : ''}
          </button>
        </div>
      </Modal>
    </div>
  );
};

export default AdminLayout;
