import { useState, Dispatch, SetStateAction } from 'react'; //   
import { motion } from 'framer-motion';
import { uploadFile, getPublicUrl } from '../lib/storage';

interface ImageUploaderProps {
  onUpload: (urls: string[]) => void; //    URL  
  onUploading?: Dispatch<SetStateAction<boolean>>; //     
}

const MAX_FILE_SIZE = 5 * 1024 * 1024; // 5MB
const ALLOWED_FILE_TYPES = ['image/jpeg', 'image/png', 'image/webp'];

export const ImageUploader = ({ onUpload, onUploading }: ImageUploaderProps) => {
  const [progress, setProgress] = useState(0); //  
  const [uploadedCount, setUploadedCount] = useState(0); //    
  const [selectedFiles, setSelectedFiles] = useState<FileList | null>(null); //  
  const [isUploading, setIsUploading] = useState(false); //  

  const handleFileUpload = async (files: FileList | null) => {
    if (isUploading) {
      alert('  .  , .');
      return;
    }

    if (!files || files.length === 0) {
      alert(' ,    ');
      return;
    }

    setIsUploading(true); //     true
    setSelectedFiles(files);
    onUploading?.(true); //     true   

    const urls: string[] = []; //    URL  
    let successCount = 0; //    

    try {
      for (let i = 0; i < files.length; i++) {
        const file = files[i];
        let publicUrl = '';

        try {
          //  
          if (!ALLOWED_FILE_TYPES.includes(file.type)) {
            throw new Error(` "${file.name}":  `);
          }

          if (file.size > MAX_FILE_SIZE) {
            throw new Error(` "${file.name}":  5MB`);
          }

          //  
          const { path } = await uploadFile(file, 'products', 'tires'); //    
          publicUrl = getPublicUrl('products', path); //   URL 

          urls.push(publicUrl); //  URL  
          successCount++; //    
        } catch (error) {
          console.error(`   ${file.name}:`, error);
          alert(error instanceof Error ? error.message : ' ');
          continue; //    ,   
        }

        //  
        setProgress(((i + 1) / files.length) * 100); //  
        setUploadedCount(successCount); //    
      }

      onUpload(urls); //   URL     
    } finally {
      setIsUploading(false); //   
      onUploading?.(false); //     false   
      setProgress(0); //  
      setUploadedCount(0); //    
    }
  };

  return (
    <div className="space-y-4">
      <label className="block">
        <input
          type="file"
          multiple
          accept={ALLOWED_FILE_TYPES.join(',')} //   
          onChange={(e) => handleFileUpload(e.target.files)} //   
          className="block w-full text-sm text-gray-500
            file:mr-4 file:py-2 file:px-4
            file:rounded-full file:border-0
            file:text-sm file:font-semibold
            file:bg-blue-50 file:text-blue-700
            hover:file:bg-blue-100"
          disabled={isUploading} //  input   
        />
      </label>

      {progress > 0 && (
        <motion.div
          initial={{ opacity: 0 }}
          animate={{ opacity: 1 }}
          className="relative pt-1"
        >
          <div className="flex h-2 overflow-hidden bg-gray-200 rounded">
            <div
              className="transition-all duration-300 ease-out bg-blue-500"
              style={{ width: `${progress}%` }} //  
            />
          </div>
          <span className="text-sm text-gray-600">
            {` ${uploadedCount}  
            ${selectedFiles?.length || 0}  
            (${Math.round(progress)}%)`} //    
          </span>
        </motion.div>
      )}
    </div>
  );
};